@import '../variables.less';

@logoHeight: 100px;

header {
  top: 0;
  background-color: @white;

  @media(max-width: 460px){
    display: none;
  }

  .stickedHeader & {
      position: fixed;
      top: -@logoHeight;
      width: 100%;
      box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2), 0 0 5px  rgba(0, 0, 0, 0.1);

    .smallLogo {
      display: inline-block;
      width: 110px;
    }
  }

  .smallLogo {
    overflow: hidden;
    width: 0;
    transition: width .3s ease;
  }

  .bigLogo {
    height: @logoHeight;
    text-align: center;
  }

  nav {
    padding: 0 2em;
    display: inline-flex;
    border-top: 1px solid @light-gray;
  }
  ul {
    display: flex;
    flex-grow: 0;
    padding: 0;
    margin: 0;
    justify-content: space-between;

    li {
      text-align: center;
      flex-grow: 0;
      flex-shrink: 0;
      list-style-type: none;

      a {
        position: relative;
        display: block;
        padding: 1em 1.5em;
        text-decoration: none;

        &:active, &:visited, & {
            color: @color-1;
        }

        &:hover {
            color: @color-1;
        }

        &.selected {
          color: @color-1;
        }

        &.selected::before {
          @size: 5px;
          content: '';
          position: absolute;
          left: calc(50% - @size);
          top: -1px;
          width: 0;
          height: 0;
          border-left: @size solid transparent;
          border-right: @size solid transparent;
          border-top: @size + 4 solid @color-1;
        }
      }
    }
  }
}