@import "variables.less";


.tabItem {
  background: @light-gray;
  //  animation: fade-in .9s ease forwards;
  overflow: hidden;
  transition: width .3s ease;
  flex-grow: 0;
  flex-shrink: 1;
  padding: 1em;
  .image {
    width: 100%;
  }
  border-radius: 50%;

  //box-shadow: 0 0 5px 5px @white;
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: scale(0);

  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.tabLinks {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.tabInput {
  &:checked, & {
    display: none;
  }
}

.tabLabel {
  display: block;
  height: 20px;
  width: 20px;
  background: @light-gray;
  color: @color-1;
  padding: .5em 1em;
  margin: .5em;
  flex-shrink: 0;
  flex-grow: 1;
  min-width: 100px;
  box-shadow: 0 0 0 rgba(0, 0, 0, .1);
 // box-shadow: 0 0 5px 5px @light-gray;
  transition: box-shadow .2s ease;
}

.tabLabelChecked() {
  & + .tabLabel {
    box-shadow: 0 0 10px rgba(0, 0, 0, .3);
  }
}

.tabSelector(@id) {
    .@{id} {
      display: none;
   // width: 0;
    }

    #@{id}:checked {
      .tabLabelChecked();
      & ~ .@{id} {
        display: inline-block !important;
       // width: 150px;
      }
    }
}


.genTabs(@tabs, @i:1) when (@i <= length(@tabs)){

  .tabSelector(extract(@tabs, @i));

  .genTabs(@tabs, @i: @i + 1);
}

@productTypes: pack_1, pack_2, pack_3;
.tabs {
  .genTabs(@productTypes);
  #all:checked {
    &~.pack_1,
    &~.pack_2,
    &~.pack_3 {
      display: inline-block !important;
    }
    .tabLabelChecked();
  }
}

.separator {
  width: 100%;
  flex-shrink: 0;
}

