@import "../../variables.less";

header.mobile {
  display: none;
}

@media(max-width: 460px){
  header.mobile {
    display: block;
    z-index: 2;
    position: fixed;
    width: 100%;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2), 0 0 5px  rgba(0, 0, 0, 0.1);
  }
}


.inner {
  text-align: center;
  padding: 1em .5em;
  height: 50px;
  background: @white;

  .openIcon {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    width: calc(50px + 2em);
    height: calc(50px + 2em);
  }

  &>*{
    flex-grow: 0;
    flex-shrink: 0;
  }
}

.menuOpener:checked+.mobileMenu {
  display: block
}

.mobileMenu {
  padding: 0;
  display: none;
  ul {
    flex-direction: column;

    li {
     width: 100%;
      a.selected::before {
        top: calc(50% - 5px);
        left: 0;
        border-left: 6px solid #74302F;
        border-bottom: 5px solid transparent;
        border-top: 5px solid transparent;
      }
    }
  }
}
