@import "variables.less";

.bg-white {
  background-color: @white;
}

.bg-blue {
  background-color: @color-3;
}

.bg-fixed {
  display: flex;
  align-items: center;
  justify-content: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
}

.bg-image-1 {
 background-image: url('https://cdn.vox-cdn.com/thumbor/p_dXGKEK7A7PU5JrALVUy7OSVTY=/0x0:1600x1067/1200x900/filters:focal(672x405:928x661)/cdn.vox-cdn.com/uploads/chorus_image/image/61811905/17492271_762003007297686_8834255425342859751_o.0.0.0.0.jpg');
}

.color-white {
 color: @white;
}

.flex {
  display: flex;

  &.columns {
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.common-padding {
  padding: 0 @Hpadding;
}

.inner-padding {
  padding: 0 @Hpadding/2;
}

.m-r-10 {
  margin-right: 10px;
}

.no-text-shadow {
  text-shadow: none;
  color: @color-1;
}

.rotated {
  transform-origin: 50% 50%;
  transform: rotate(-3deg);
}

.custom-button(){
  border: none;
  border-radius: 0;
  padding: 1em .6em;
  text-align: center;
}