@import "variables.less";

.modalContent {
  display: none;
}

.container {
    padding: 1em 2em;
    position: fixed;
    height: auto;
    min-width: 30vw;
    margin: 10vw 10vw;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: @light-gray;
    box-shadow: 0 0 10px rgba(0, 0, 0, .3);
    opacity: 0;
    animation: fade-in .2s ease forwards;
    border-radius: 2px;
  }



.vell {
   position: fixed;
   background: black;
   background: rgba(0,0,0,.4);
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
  opacity: 0;
   animation: fade-in .2s ease forwards;
  background-image: linear-gradient(45deg, black, transparent);
 }

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


.modalCloser, .openOpener {
  display: none;
}

.modalCloserLabel{
    padding: .5em 1em;
  }

.openOpener:checked&+.modalContent {
    display: block;
    z-index: 5;
}
