@import 'variables.less';

.hrBlock {
  display: inline-block;
  width: 180px;
  margin-bottom: 32px;
  margin-top: 5px;

  hr {
    margin: 3px 0;
  }
}

.center {
  text-align: center;
}

.left {
  text-align: left;
}

.lobster {
  font-family: 'Lobster', cursive;
}

.massive {
  font-size: 26vw;
  text-shadow: 1vw 1vh @color-3;
  position: relative;
  top: 6vh;
}

h1 {
  color: @white;
  .bg-white & {
    color: @color-1;
  }
  font-size: 3em;
  //text-shadow: 2px 2px @color-1;
  margin: .3em 0;
}

h2, .h2 {
  font-size: 3em;
  .bg-white & {
    color: @color-1;
  }
  color: @white;
  //text-shadow: 2px 2px @color-3;

}

h3, .h3 {
  font-size: 2em;
  color: @light-gray;
  .bg-white & {
    color: @color-1;
  }
  //text-shadow: 1px 1px @color-3;
  font-weight: 300;
  @media(max-width: 460px){
    font-size: 1.2em;
  }
}

h4 {
  font-size: 1.5em;
  color: @color-1;
  font-weight: 300;
//  text-shadow: 3px 3px @color-1;

}

.column {
  flex-shrink: 0;
  width: 40ch;
  flex-grow: .3;
}