@import 'layout/section.less';
@import 'layout/header.less';
@import 'layout/mobile/header.less';
@import 'typography.less';
@import 'helpers.less';
@import 'layout/card.less';
@import 'tabs.less';
@import 'modal.less';
@import "variables.less";


img {
  position: relative;
  &:before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: @light-gray;
  }
}

html, body {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  font-family: 'Nunito', sans-serif;
  background: @color-1;
}

main {
  display: flex;
  flex-direction: column;
}

@Hpadding: 10vw;
@Mpadding: 15px;

section {
  position: relative;
  padding: 0 @Hpadding;
  @media(max-width: 460px){
    padding: 0 @Mpadding;
  }

  &.full-page {
    min-height: 100vh;
   //min-height: 800px;
  }
}


button {
  .custom-button();
    &.mainButton {
      max-width: 200px;
      font-size: 1em;
      padding: .6em 1em;
    }
}

.next-button {
  .custom-button();
  position: absolute;
  top: 80vh;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  background: @color-4;
  box-shadow: .5vw  .5vh 0 @color-3;

  &:focus {
    outline: none;
  }
}

.rombInner {
  transform: rotate(45deg);
  background: @color-1;
  height: 150px;
  width: 150px;
  border-radius: 10px;
  box-shadow: .5vw .5vh 0 @color-3;
}

.romb {
  flex-shrink: 0;
  flex-grow: 0;
}